.user-calendar-cell {
  cursor: pointer;
}

h2{
  text-transform: capitalize;
}

.user-calendar-grid {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
}

.user-calendar-row {
  display: flex;
}

.user-calendar-cell {
  border: 1px solid #ddd;
  padding: 1px;
  text-align: center;
  width: 35px;
  height: 20px;
  font-size: 0.7rem;
}


.user-header {
  font-weight: bold;
  background-color: #f4f4f4;
  height: 100px;
  display: flex; /* Align the rotated text properly */
  justify-content: center;
  align-items: center;
}

.user-header span {
  display: inline-block; /* Required for transform */
  transform: rotate(-45deg); /* Apply rotation to the text */
  white-space: nowrap; /* Prevent text from wrapping */
} 
.user-header span{
  text-transform: capitalize;
}

.day-number {
  font-weight: bold;
}

.day-name {
  font-style: italic;
}

/* Colors for attendance */
.present {
  background-color: green;
  color: white;
}

.absent {
  background-color: red;
  color: white;
}
.loading {
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
  margin: 20px 0;
  animation: blink 1.5s linear infinite;
  color: #333;
}

@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
